import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Chip from '@material-ui/core/Chip';
import FileCopy from '@material-ui/icons/FileCopy';
import {CopyToClipboard} from 'react-copy-to-clipboard';
import init from '../functions/shorten.js';

const styles = theme => ({
    root: {
        width: '90%',
    },
    button: {
        marginRight: theme.spacing.unit,
    },
    instructions: {
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
    },
});

function getSteps() {
    return ['Insira seu whatsapp', 'Código gerado!', 'Encurtar link'];
}

function getStepContent(step) {
    switch (step) {
        case 0:
        return 'Insira seu whatsapp...';
        case 1:
        return 'Copie o seu código!'
        case 2:
        return 'Copie o seu link encurtado!';
        default:
        return 'Unknown step';
    }
}

class HorizontalLinearStepper extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            activeStep: 0,
            skipped: new Set(),
            link: '',
            shortLink: 'Não gerado'
        };
        this.getShort = this.getShort.bind(this)
    }

    getShort = async (url) => await init(url).then(function(result) {
        return result
    })


    isStepOptional = step => step === 2;

    handleNext = () => {

        let {number, message, changeStep} = this.props
        changeStep(activeStep)
        const { activeStep } = this.state;


        let { skipped } = this.state;

        if (activeStep === 0){
            var link = `https://api.whatsapp.com/send?phone=${number()}&text=${message()}`
            this.setState({'link': link})
        }

        if (activeStep === 1){
            let currentComponent = this;
            init(this.state.link).then(function(result) {
                currentComponent.setState({'shortLink': result.url})
            })
        }

        if (this.isStepSkipped(activeStep)) {
            skipped = new Set(skipped.values());
            skipped.delete(activeStep);
        }

        this.setState({
            activeStep: activeStep + 1,
            skipped,
        });


    };

    handleBack = () => {
        const {changeStep} = this.props
        changeStep(this.state.activeStep)
        this.setState(state => ({
            activeStep: state.activeStep - 1,
        }));


    };

    handleSkip = () => {
        const { activeStep } = this.state;
        if (!this.isStepOptional(activeStep)) {
            throw new Error("You can't skip a step that isn't optional.");
        }

        this.setState(state => {
            const skipped = new Set(state.skipped.values());
            skipped.add(activeStep);
            return {
                activeStep: state.activeStep + 1,
                skipped,
            };
        });
    };

    handleReset = () => {
        const { hardReset } = this.props
        hardReset()

        this.setState({
            activeStep: 0,
        });
    };

    isStepSkipped(step) {
        return this.state.skipped.has(step);
    }

    render() {
        const { classes } = this.props;
        const steps = getSteps();
        const { activeStep } = this.state;

        return (
            <div className={classes.root} style={{'width': '100%',}}>
                <Stepper activeStep={activeStep}>
                    {steps.map((label, index) => {
                        const props = {};
                        const labelProps = {};

                        if (this.isStepOptional(index)) {
                            labelProps.optional = <Typography variant="caption">Opcional</Typography>;
                            }

                            if (this.isStepSkipped(index)) {
                                props.completed = false;
                            }

                            return (
                                <Step key={label} {...props}>
                                    <StepLabel {...labelProps}>{label}</StepLabel>
                                </Step>
                            );
                        })}
                    </Stepper>
                    <div>
                        {activeStep === steps.length ? (
                            <div>
                                <Typography className={classes.instructions}>
                                    Todos passos completos, você já pode usar seu link.
                                </Typography>
                                <Button onClick={this.handleReset} className={classes.button}>
                                    Criar outro
                                </Button>
                            </div>
                        ) : (
                            <div style={{'text-align': 'center'}}>

                                <Typography className={classes.instructions}>
                                    { activeStep < 1 ? getStepContent(activeStep) : ''}
                                </Typography>
                                <div style={{'padding': '10px 0', }}>
                                    {activeStep >= 1 && (
                                        <div>
                                            <p>{activeStep === 1 && activeStep < 2 ? this.state.link : this.state.shortLink}</p>
                                            <CopyToClipboard text={activeStep === 1 && activeStep < 2 ? this.state.link : this.state.shortLink}
                                                onCopy={() => this.setState({copied: true})}>
                                                <Chip
                                                    icon={<FileCopy />}
                                                    label={getStepContent(activeStep)}
                                                    clickable

                                                    />
                                            </CopyToClipboard>
                                        </div>

                                    )}
                                </div>

                                <div>
                                    <Button
                                        disabled={activeStep === 0}
                                        onClick={this.handleBack}
                                        className={classes.button}
                                        >
                                        Voltar
                                    </Button>

                                    {this.isStepOptional(activeStep) && (
                                        <Button
                                            variant="contained"
                                            color="primary"
                                            onClick={this.handleSkip}
                                            className={classes.button}
                                            >
                                            Pular
                                        </Button>
                                    )}

                                    <Button
                                        variant="contained"
                                        color="primary"
                                        onClick={this.handleNext}
                                        className={classes.button}
                                        >
                                        {activeStep === steps.length - 1 ? 'Pronto' : 'Próximo'}
                                    </Button>
                                </div>
                            </div>
                        )}
                    </div>
                </div>
            );
        }
    }

    HorizontalLinearStepper.propTypes = {
        classes: PropTypes.object,
    };

    export default withStyles(styles)(HorizontalLinearStepper);
